<template>
  <div class="order">
    <van-nav-bar
      title="我的订单"
      left-text="返回"
      left-arrow
      @click-left="back"
      fixed
    />
    <div class="content">
      <van-tabs v-model="activeName" sticky offset-top=46  @change="toggleTab">
        <van-tab title="全部订单" name="0">

          <div class="order-box" v-for="(order,index) in orderDatas" :key="index">
            <div class="order-top clearfix">
              <div class="order-no fl">{{order.oid}}</div>
              <div class="order-status fr">{{order.status == 1 ? '待收货':'已完成'}}</div>
            </div>
            <div class="order-middle">

              <div class="item" v-for="(item,i) in order.products" :key="i">
                <div class="item-left">
                  <div class="name">{{item.name}}</div>
                  <div class="rule">{{item.rule}}</div>
                </div>
                <div class="count">x{{item.count}}</div>
                <div class="price">￥{{item.price}}</div>
              </div>

            </div>
            <div class="order-bottom">
              <div class="order-total-box clearfix">
                <div class="order-count fl">共计 {{order.count}} 件商品</div>
                <div class="order-price fr">合计￥{{order.totalPrice}}</div>
              </div>
              <div class="clearfix">
                <div class="order-time fl">
                  {{order.createdAt | formatDate('yyyy-MM-dd hh:mm:ss')}}
                </div>
                <div class="receive-btn fr" v-if="order.status == 1" @click="handleOrder('./receive', order.oid, index)">确认收货</div>
                <van-icon class="delete-btn fr" v-else name="delete" @click="showDeleteDialog('./removeOrder', order.oid, index)" />
              </div>
            </div>
          </div>

        </van-tab>
        <van-tab title="待收货" name="1">

          <div class="order-box" v-for="(order,index) in orderDatas" :key="index">
            <div class="order-top clearfix">
              <div class="order-no fl">{{order.oid}}</div>
              <div class="order-status fr">{{order.status == 1 ? '待收货':'已完成'}}</div>
            </div>
            <div class="order-middle">

              <div class="item" v-for="(item,i) in order.products" :key="i">
                <div class="item-left">
                  <div class="name">{{item.name}}</div>
                  <div class="rule">{{item.rule}}</div>
                </div>
                <div class="count">x{{item.count}}</div>
                <div class="price">￥{{item.price}}</div>
              </div>

            </div>
            <div class="order-bottom">
              <div class="order-total-box clearfix">
                <div class="order-count fl">共计 {{order.count}} 件商品</div>
                <div class="order-price fr">合计￥{{order.totalPrice}}</div>
              </div>
              <div class="clearfix">
                <div class="order-time fl">
                  {{order.createdAt | formatDate('yyyy-MM-dd hh:mm:ss')}}
                </div>
                <div class="receive-btn fr" v-if="order.status == 1" @click="handleOrder('./receive', order.oid, index)">确认收货</div>
                <van-icon class="delete-btn fr" v-else name="delete" @click="handleOrder('./removeOrder', order.oid, index)" />
              </div>
            </div>
          </div>

        </van-tab>
        <van-tab title="已收货" name="2">

          <div class="order-box" v-for="(order,index) in orderDatas" :key="index">
            <div class="order-top clearfix">
              <div class="order-no fl">{{order.oid}}</div>
              <div class="order-status fr">{{order.status == 1 ? '待收货':'已完成'}}</div>
            </div>
            <div class="order-middle">

              <div class="item" v-for="(item,i) in order.products" :key="i">
                <div class="item-left">
                  <div class="name">{{item.name}}</div>
                  <div class="rule">{{item.rule}}</div>
                </div>
                <div class="count">x{{item.count}}</div>
                <div class="price">￥{{item.price}}</div>
              </div>

            </div>
            <div class="order-bottom">
              <div class="order-total-box clearfix">
                <div class="order-count fl">共计 {{order.count}} 件商品</div>
                <div class="order-price fr">合计￥{{order.totalPrice}}</div>
              </div>
              <div class="clearfix">
                <div class="order-time fl">
                  {{order.createdAt | formatDate('yyyy-MM-dd hh:mm:ss')}}
                </div>
                <div class="receive-btn fr" v-if="order.status == 1" @click="handleOrder('./receive', order.oid, index)">确认收货</div>
                <van-icon class="delete-btn fr" v-else name="delete" @click="handleOrder('./removeOrder', order.oid, index)" />
              </div>
            </div>
          </div>

        </van-tab>
      </van-tabs>

      <van-empty
        class="custom-image"
        image="https://img.yzcdn.cn/vant/custom-empty-image.png"
        description="没有相关的订单"
        v-if="orderDatas.length == 0"
      />
    </div>
  </div>
</template>

<script>
  import "../assets/less/order.less"
  import {utils} from "../assets/js/utils"
  export default {
    data(){
      return{
        activeName: '0',
        orderDatas: [],
      }
    },
    created(){
      this.getOrderData(0);
    },
    methods: {
      back(){
        this.$router.push({name: "My"})
      },
      getOrderData(status){
        let tk = localStorage.getItem('CSTK');
        // 不存在token
        if(!tk){
          return;
        }

        this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
        });

        this.orderDatas = [];

        this.axios({
          method: 'GET',
          url:'/findOrder',
          params: {
            appkey: this.appkey,
            tokenString: tk,
            status,       
          }
        }).then(result => {
          this.$toast.clear();
          // console.log("findOrder result ==> ",result)
          if(result.data.code == 70000){
            // NO231534:{
            //   oid: NO6146846,
            //   createdAt:2012-55,
            //   address:,
            //   receiver:,
            //   totalPrice:,
            //   products: [
            //     {
            //       name:,
            //       count:,
            //       price:,
            //       rule:,
            //     }
            //   ]
            // }
            let orderData = {};
            result.data.result.map(v => {
              if(!orderData[v.oid]){
                // 如果不存在盖订单号，则初始化一个对象
                orderData[v.oid] = {
                  oid: v.oid,
                  createdAt: v.createdAt,
                  address: v.address,
                  receiver: v.receiver,
                  totalPrice: v.count * v.price,
                  count: v.count,
                  products:[v],
                  status: v.status,
                }
              }else{
                // 存在则添加产品到products中
                orderData[v.oid].products.push(v);
                orderData[v.oid].totalPrice += v.count * v.price;
                orderData[v.oid].count += v.count;
              }
            })

            // 时间降序排序
            for(let key in orderData){
              this.orderDatas.push(orderData[key]);
            }
            this.orderDatas.sort((a,b)=>{
              return new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime();
            })

            // // console.log("orderDatas ==> ",this.orderDatas)


          }
        }).catch(err =>{
          this.$toast.clear();
          // console.log("findOrder err ==> ",err)
        })
      

      },
      // 切换标签页
      toggleTab(name,title){
        this.getOrderData(name)
      },
      // 确认收货与删除订单
      handleOrder(url, oid, index){
        let tk = localStorage.getItem('CSTK');
        // 不存在token
        if(!tk){
          return;
        }

        this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
        });

        let data = utils.queryString(
          {
            appkey: this.appkey,
            tokenString: tk,
            oid
          }
        ) 
        

        this.axios({
          method: 'POST',
          url,
          data
        }).then(result => {
          this.$toast.clear();
          // console.log(url, " result ==> ",result)
          this.$toast(result.data.msg)
          if(result.data.code == 80000){
            this.orderDatas[index].status = 2;
          }
          if(result.data.code == 90000){
            this.orderDatas.splice(index, 1);
          }
        }).catch(err =>{
          this.$toast.clear();
          // console.log(url, " err ==> ",err)
        })
       
      },
      // 删除订单 弹出框
      showDeleteDialog(url, oid, index){
        this.$dialog.confirm({
          message: '确定要删除订单吗？',
        })
          .then(() => {
            this.handleOrder(url, oid, index);
          })
          .catch(() => {
            // on cancel
          });
      }
    }
  }
</script>
